import React, { useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import Navbar from './Navbar';
import Footer from './Footer';
import '../css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import 'bootstrap-icons/font/bootstrap-icons.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import '../css/home.css';
import '../css/style.css';
import '../css/lecturelist.css';

const LectureList = () => {
    const navigate = useNavigate();

    useEffect(() => {
        const dateElement = document.querySelector('.date');
        const currentDate = new Date();
        const year = currentDate.getFullYear();
        const month = String(currentDate.getMonth() + 1).padStart(2, '0');
        const day = String(currentDate.getDate()).padStart(2, '0');
        dateElement.textContent = `${year}년 ${month}월 ${day}일`;
    }, []);

    const handleEnterClick = () => {
        navigate('/video');
    };

    return (
        <div className="container-xxl bg-white p-0">
            <div className="container-xxl position-relative p-0">
                <Navbar />
                <div className="container-xxl py-5 bg-primary hero-header mb-5">
                    <div className="container my-5 py-5 px-lg-5">
                        <div className="row g-5 py-5">
                            <div className="col-12 text-center">
                                <h1 className="text-white animated zoomIn">Lecture Schedule</h1>
                                <hr className="bg-white mx-auto mt-0" style={{ width: '90px' }} />
                            </div>
                        </div>
                    </div>
                </div>

                <div className="container lecture-container">
                    <div className="lecture-header">
                        <h2>Lecture Schedule</h2>
                        <div className="date"></div>
                    </div>
                    <div className="lecture-item">
                        <div>
                            <h3>Django 활용 Web App 개발</h3>
                            <div className="time">09:30 - 17:30</div>
                        </div>
                        <div className="actions">
                            <a href="pdfview.html" target="_blank" rel="noopener noreferrer">교안보기</a>
                            <button onClick={handleEnterClick}>입장</button>
                        </div>
                    </div>
                    <div className="lecture-item">
                        <div>
                            <h3>Django 활용 Web App 개발</h3>
                            <div className="time">18:00 - 19:00</div>
                        </div>
                        <div className="actions">
                            <a href="quiz.html" target="_blank" rel="noopener noreferrer">퀴즈풀기</a>
                        </div>
                    </div>
                </div>
                <Footer />
            </div>
            <a href="#" className="btn btn-lg btn-primary btn-lg-square back-to-top pt-2"><i className="bi bi-arrow-up"></i></a>
        </div>
    );
};

export default LectureList;
