import React from 'react';

const NoticeSection = () => (
    <div className="col-md-6">
        <div className="section-box">
            <h6 className="section-title">공지사항</h6>
            <a href="#" className="see-all">See all</a>
            <ul className="notice-list">
                <NoticeItem badge="안내" text="LMS 시스템 정기 점검 안내" time="10:25 am" />
                <NoticeItem badge="안내" text="사용참고 자료 안내" time="12:35 pm" />
                <NoticeItem badge="안내" text="이번 주 일정 및 공지사항" time="04:30 pm" />
            </ul>
        </div>
    </div>
);

const NoticeItem = ({ badge, text, time }) => (
    <li>
        <span className="badge">{badge}</span>
        <span className="notice-text">{text}</span>
        <span className="notice-time">{time}</span>
    </li>
);

export default NoticeSection;